<template>
  <div class="home bg-white">
    <header class="myheader px-5 d-flex bg-white border-b ai-center text-black">
      <div class="flex-1">
        <div class="d-flex ai-center">
          <h1 class="menuheder text-green mr-4 iconfont icon-menu" @click="menu"></h1>
          <h1 class="text-green iconfont icon-git"></h1>
          <h2 class="mytitle flex-1 ml-4">文档</h2>
        </div>
      </div>
      <div class="d-flex ai-center"> 
       
        <el-input size="mini" class="headerinp pr-3" v-model="input" placeholder="请输入内容"></el-input>
        <ul class="ul-header d-flex ai-center">
          <li
            class="text-black mr-5"
            v-for="(item,index) in data1"
            :key="index"
            @click="add(index)"
            :class="{active:cur==index}"
          >{{item}}</li>
        </ul>
      </div>
    </header>
    <Main class="d-flex">
      <div class="menu pl-4">
        <el-aside width="200px" style="height: 100vh;">
          <el-menu router >
            <menumytab title=" HTML" icon="icon-HTML" index="1" :menu="html"></menumytab>
            <menumytab title=" Css" icon="icon-scss" index="2" :menu="css"></menumytab>
            <menumytab title=" JavaScript" icon="icon-JavaScript" index="3" :menu="JavaScript"></menumytab>
            <menumytab title=" Node.js" icon="icon-js" index="4" :menu="node"></menumytab>
            <menumytab title=" MongoDB" icon="icon-ziyuan" index="5" :menu="mongodb"></menumytab>
          </el-menu>
        </el-aside>
      </div>
      <transition name="fade">
        <div v-if="show" class="mymenu"> 
          <el-aside width="200px" style="height: 100vh;">
            <el-menu router :default-openeds="['1', '3']">
              <menumytab title=" HTML" icon="icon-HTML" index="1" :menu="html"></menumytab>
              <menumytab title=" Css" icon="icon-scss" index="2" :menu="css"></menumytab>
              <menumytab title=" JavaScript" icon="icon-JavaScript" index="3" :menu="JavaScript"></menumytab>
              <menumytab title=" Node.js" icon="icon-js" index="4" :menu="node"></menumytab>
              <menumytab title=" MongoDB" icon="icon-ziyuan" index="5" :menu="mongodb"></menumytab>
            </el-menu>
          </el-aside>
        </div>
      </transition>
      <div class="say1 pr-4 text-center flex-1" @click.stop="menu1">
        <div> 
          <transition name="fade" mode="out-in">
            <router-view></router-view>
          </transition>
        </div>
      </div>
    </Main>
  </div>
</template>
<style>
.myheader{
  position: sticky;
     top: 0;
     z-index: 999;
}
.menuheder:hover {
  color: #304455;
}
.mymenu {
  background-color: #fff;
  height: 100%;
  position: fixed;
  z-index: 99;
}
.el-menu {

  border-right: none;
}

.el-aside {
  color: #333;
  overflow-x: hidden !important;
overflow-y: auto !important;

}
.ul-header .active {
  border-bottom: solid white;
}
.ul-header li:hover {
  border-bottom: solid #42b983;
  cursor:Pointer;
}
.ul-header {
  
  list-style: none;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>

<script>
import router from "../router";
export default {
  data() {
    return {
      show: false,
      cur: 0,
      input: "",
      data1: ["HTML", "Css", "JavaScript", "Node.js", "MongoDB"],
      html: [],
      css: [],
      JavaScript: [],
      node: [],
      mongodb: []
    };
  },
  methods: {
    add(index) {
      this.cur = index;
      if (index == 0&& this.cur==0) {
        router.push("/websocket");
      }
      if (index == 1) {
        router.push("/sass");
      }
      if (index == 2) {
        router.push("/array");
      }
      if (index == 3) {
        router.push("/express");
      }
      if (index == 4) {
        router.push("/mongodb");
      }
    },
    menu() {
      this.show = !this.show;
    },
    menu1() {
      if (this.show) {
        this.show = !this.show;
      }
    },
    async ferchjs() {
      const res = await this.$http.get("/js");
      this.JavaScript = res.data;
      
    },
    async ferchhtml() {
      const res = await this.$http.get("/html");
      this.html = res.data;
    },
    async ferchcss() {
      const res = await this.$http.get("/css");
      this.css = res.data;
    },
    async ferchnode() {
      const res = await this.$http.get("/node");
      this.node = res.data;
    },
    async ferchmongodb() {
      const res = await this.$http.get("/mongodb");
      this.mongodb = res.data;
    }
  },
  created() {
    this.ferchhtml();
    this.ferchjs();
    this.ferchcss();
    this.ferchnode()
    this.ferchmongodb()
  },
  watch:{
  $route(to,from){
    if(to!==from &&document.documentElement.clientWidth<=765){
       this.show = !this.show;
    }
  }
},
};
</script>
