import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
 
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/websocket',
      children:[
        {path:'/Array',component: Array => require(['./views/Javascript/Array'],Array),props:true},
        {path:'/clone',component: clone => require(['./views/Javascript/clone'],clone)},
        {path:'/changethis',component: changethis=> require(['./views/Javascript/changethis'],changethis)},
        {path:'/mvvm',component: mvvm => require(['./views/Javascript/mvvm'],mvvm)},
        {path:'/string',component: string => require(['./views/Javascript/string'],string)},
        {path:'/myfunction',component: myfunction => require(['./views/Javascript/myfunction'],myfunction)},
        {path:'/AJAX',component:AJAX => require(['./views/Javascript/AJAX'],AJAX)},
        {path:'/Promise',component:Promise => require(['./views/Javascript/Promise'],Promise)}, 
        {path:'/Generator',component:Generator => require(['./views/Javascript/Generator'],Generator)},
        {path:'/asyncawait',component:asyncawait => require(['./views/Javascript/asyncawait'],asyncawait)},   

        {path:'/websocket',component:websocket => require(['./views/html/websocket'],websocket)},
        {path:'/RegExp',component:RegExp => require(['./views/html/RegExp'],RegExp)},

        {path:'/sass',component:sass => require(['./views/css/sass'],sass)},
        {path:'/flex',component:flex => require(['./views/css/flex'],flex)},
        {path:'/cssAll',component:cssAll => require(['./views/css/cssAll'],cssAll)},

        {path:'/express',component:express => require(['./views/node/express'],express)},

        {path:'/mongodb',component:mongodb => require(['./views/mongodb/mongodb'],mongodb)},
        {path:'/sdui',component:sdui => require(['./views/mongodb/sdui'],sdui)},
        {path:'/type',component:type => require(['./views/mongodb/type'],type)},
        {path:'/backup',component:backup => require(['./views/mongodb/backup'],backup)},


        
      ]
    },
  ]
})
